$my-favorite-color: #fff000;

@mixin text-setting(){
  color:#fff;
  text-align: center;
};

*{
  @include text-setting;
}
html{
  background-image: url("../images/bg.png"); // style.scss style.scss
}

body{
  background-color: rgba(255,255,255,.6);
}

.container{
  background-color: rgba(0,0,0,.6);
}

.style-scss-test{
  color: $my-favorite-color;
  &:hover{
    transform: scaleX(3);
  }
}